* {
  box-sizing: border-box;
}

body {
  color: #433c3c;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.loader {
  display: inline-block;
  position: relative;
}

.loader:before {
  content: "Loading";
  color: #fff;
  letter-spacing: 2px;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  animation: floating 1s ease-out infinite alternate;
  display: inline-block;
}

.loader:after {
  content: "";
  width: 100%;
  height: 10px;
  filter: blur(4px);
  box-sizing: border-box;
  background: rgba(0, 0, 0, .15);
  border-radius: 50%;
  animation: animloader 1s ease-out infinite alternate;
  position: absolute;
  top: 100%;
  left: 0;
}

@keyframes floating {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-25px);
  }
}

@keyframes animloader {
  0% {
    transform: scale(.8);
  }

  100% {
    transform: scale(1.2);
  }
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.cat-info {
  width: 100%;
  gap: 45px;
  margin-top: 40px;
  display: flex;
}

.blok-info {
  width: 500px;
  text-align: left;
  font-size: 18px;
}

.breed-select {
  max-width: 400px;
  margin: auto;
  display: none;
}

